<template>
  <section style="background-color: #fff;">
    <mt-cell
      title="选择俱乐部类型"
      :value="clubType.label"
      is-link
      @click.native="sheetClubType.visible=true">
    </mt-cell>
    <mt-actionsheet
      cancelText=""
      :actions="sheetClubType.actions"
      v-model="sheetClubType.visible">
    </mt-actionsheet>
    <mt-field label="俱乐部名称" placeholder="请输入俱乐部名称" class="right" v-model="name"></mt-field>
    <mt-cell
      title="选择俱乐部所在城市"
      :value="city.label"
      is-link
      @click.native="sheetCity.visible=true">
    </mt-cell>
    <mt-actionsheet
      cancelText=""
      :actions="sheetCity.actions"
      v-model="sheetCity.visible">
    </mt-actionsheet>
    <mt-field type="tel" label="俱乐部联系方式" placeholder="请填写正确的手机号码用于收取短信" v-model="phone"></mt-field>
    <mt-field type="mail" label="邮箱" placeholder="请填写邮箱" class="right" v-model="email"></mt-field>
    <mt-field label="微信号" placeholder="请填写微信号" class="right" v-model="wxId"></mt-field>
    <mt-field type="number" label="银行账号" placeholder="请填写银行账号" class="right" v-model="bankNo"></mt-field>
    <mt-cell title="俱乐部头像"></mt-cell>
    <section class="upload-image">
      <ul>
        <li>
          <template v-if="headimg.base64.length">
            <img class="uploadImage" :src="headimg.base64"/>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'headimg')"></file-image>
          </template>
        </li>
      </ul>
    </section>
    <mt-cell title="俱乐部主页图" value="请上传横图"></mt-cell>
    <section class="upload-image">
      <ul>
        <li>
          <template v-if="mainimg.base64.length">
            <img class="uploadImage" :src="mainimg.base64"/>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'mainimg')"></file-image>
          </template>
        </li>
      </ul>
    </section>
    <mt-cell title="俱乐部简介"></mt-cell>
    <mt-field type="textarea" placeholder="请填写俱乐部简介" v-model="desc"></mt-field>
    <mt-cell title="上传认证材料" value="请上传您的身份证正反面和营业执照"></mt-cell>
    <section class="upload-image">
      <ul>
        <li>
          <template v-if="idcardF.base64.length">
            <img class="uploadImage" :src="idcardF.base64"/>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'idcardF')"></file-image>
          </template>
          <h4>身份证正面</h4>
        </li>
        <li>
          <template v-if="idcardB.base64.length">
            <img class="uploadImage" :src="idcardB.base64"/>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'idcardB')"></file-image>
          </template>
          <h4>身份证反面</h4>
        </li>
        <li>
          <template v-if="licenceimg.base64.length">
            <img class="uploadImage" :src="licenceimg.base64"/>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'licenceimg')"></file-image>
          </template>
          <h4>营业执照</h4>
        </li>
      </ul>
    </section>
    <section class="ft">
      <p class="agreement"><input type="checkbox"/><a href="">同意《号角平台商户服务协议》</a></p>
      <mt-button type="primary" @click="handSubmit">确认</mt-button>
    </section>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import FileImage from '@/components/file-image.vue'

  export default {
    name: 'club',
    components: {
      FileImage
    },
    data () {
      return {
        CityList: [],
        clubType: {
          value: -1,
          label: '请选择'
        },
        sheetClubType: {
          visible: false,
          actions: []
        },
        name: '',
        city: {
          value: -1,
          label: '请选择'
        },
        sheetCity: {
          visible: false,
          actions: []
        },
        phone: null,
        email: '',
        wxId: '',
        bankNo: null,
        desc: '',
        headimg: {
          file: null,
          base64: '',
          url: ''
        },
        mainimg: {
          file: null,
          base64: '',
          url: ''
        },
        idcardF: {
          file: null,
          base64: '',
          url: ''
        },
        idcardB: {
          file: null,
          base64: '',
          url: ''
        },
        licenceimg: {
          file: null,
          base64: '',
          url: ''
        },
      }
    },
    computed: {
      ...mapState({
        ClubType: state => state.config.ClubType,
      })
    },
    watch: {
      ClubType: {
        immediate: true,
        handler (value) {
          value.forEach(e => {
            this.sheetClubType.actions.push({
              name: e.label,
              method: () => {
                this.clubType = e
              }
            })
          })
        }
      },
      CityList: {
        immediate: true,
        handler (value) {
          value.forEach(e => {
            this.sheetCity.actions.push({
              name: e.label,
              method: () => {
                this.city = e
              }
            })
          })
        }
      }
    },
    created () {
      for (let i = 0; i < 10; i++) {
        this.CityList.push({
          value: `000${i}`,
          label: `城市${i}`
        })
      }
    },
    methods: {
      uploadImg (file) {
        return new Promise((resolve, reject) => {
          let fd = new FormData()
          fd.append('imgType', 1)
          fd.append('upload', file)
          this.$http.post(this.$port.UPLOAD_IMG, fd).then(res => {
            resolve(res)
          })
        })
      },
      handLoadImg (e, file, prop) {
        console.log('handLoadImg', e, file, prop)
        this[prop].file = file
        this[prop].base64 = e.currentTarget.result
        this.uploadImg(file).then(res => {
          if (res.code === 200) {
            this[prop].url = res.data
          } else {
            alert(res.msg)
          }
        })
      },
      handSubmit () {
        let params = {
          clubId: 0,
          clubType: this.clubType.value,
          name: this.name,
          city: this.city,
          phone: this.phone,
          email: this.email,
          wxId: this.wxId,
          bankNo: this.bankNo,
          headimg: this.headimg.url,
          mainimg: this.mainimg.url,
          idcardF: this.idcardF.url,
          idcardB: this.idcardB.url,
          licenceimg: this.licenceimg.url,
          desc: this.desc
        }
        this.$http.post(this.$port.AUTH_CLUB, this.$qs.stringify(params)).then(res => {
          if (res.code === 200) {

          }
          alert(res.msg)
        })
      }
    }
  }
</script>
<style lang="scss">
</style>
<style lang="scss" scoped>
  .upload-image {
    background-color: #fff;
    padding: 0 10px;
    ul {
      display: flex;
      li {
        flex: 1;
        h4 {
          font-size: 12px;
          color: #333;
        }
        h4 {
          text-align: center;
          font-weight: 400;
          color: #333;
          padding-top: 5px;
        }
      }
    }
    .ui-upload {
      width: 110px;
      height: 110px;
    }
  }

  .uploadImage {
    width: 110px;
    height: 110px;
    border: solid #e5e5e5 1px;
    object-fit: contain;
    display: block;
  }

  .ft {
    padding: 15px 10px;
    border-top: solid #e5e5e5 1px;
    .agreement {
      font-size: 12px;
      margin-bottom: 10px;
      input[type='checkbox'] {
        margin-right: 5px;
        vertical-align: middle;
      }
    }
    .mint-button {
      width: 100%;
    }
  }
</style>



